




























import { Component, Vue } from "vue-property-decorator";
import Message from "../components/Message.vue";
import { MyHero } from "../Hero";
@Component({
  components: {
    Message
  }
})
export default class Dashboard extends Vue {
  private title: string = "Top Heroes";
  private searchTitle: string = "Hero Search";
  private searchValue: string = "";
  // private fixedHeroList: MyHero[] = [];
  private topHero: MyHero[] = [];
  public goDetail(id: number): void {
    this.$store.commit("addMessage", `fetched hero id=${id}`);
    this.$store.state.fixedHeroList = [];
    // @ts-ignore
    this.$router.push({
      name: "Detail",
      params: {
        id: id
      }
    });
  }
  public searchHero(): void {
    this.$store.commit("searchHero", this.searchValue);
    this.$store.commit('addMessage',`found heroes matching "${this.searchValue}"`)
  }

  get fixedHeroList():void {
    return this.$store.state.fixedHeroList
  }
  private created(): void {
    this.topHero = this.$store.state.topHeroList;
    // this.fixedHeroList = this.$store.state.fixedHeroList;
    this.$store.commit("addMessage", "fetched heroes");
  }
}
