import Vue from 'vue'
import Vuex from 'vuex'
import { MyHero } from './Hero'
import { HeroList, MessageList, fixedHeroList } from './json'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    HeroList,
    MessageList,
    topHeroList: HeroList.slice(0, 4),
    fixedHeroList,
    idNum:21
  },
  mutations: {
    /**
     * 添加新英雄
     * @param hero 新增英雄信息
     */
    addHero(state,hero:MyHero): void {
      state.HeroList.push(hero);
    },
    /**
     * 更改英雄信息
     * @param hero 修改英雄信息
     */
     editHero(state,hero:MyHero): void {
      state.HeroList.forEach((v: MyHero, i: number) => {
        if (v.id === hero.id) {
          v.name = hero.name;
        }
      })
    },
    /**
     * 删除英雄
     * @param id 英雄id
     */
     deleteHero(state,id: number): void {
      state.HeroList.forEach((v: MyHero, i: number) => {
        if (v.id === id) {
          state.HeroList.splice(i, 1);
        }
      })
    },
    /**
     * 查询匹配英雄
     * @param searchValue 查询字段
     */
     searchHero(state,searchValue: string):void {
       if(!searchValue){
        state.fixedHeroList = [];
        return;
       }
      let fixedHeroList: MyHero[] = [];
      fixedHeroList = state.HeroList.filter((v: MyHero, i: number) => {
        return v.name.toLowerCase().indexOf(searchValue.toLowerCase()) > -1;
      })
       state.fixedHeroList = fixedHeroList;
    },

    /**
     * 更新top
     */
     freshTop(state):void {
       state.HeroList.slice(0, 3);
    },

    /**
   * 新增操作信息
   * @param msg 用户操作信息
   */
   addMessage(state,msg: string): void {
    let str = "HeroService: ";
    str += msg;
    state.MessageList.push(str);
  },
   clearMessage(state): void {
     console.log(1)
    state.MessageList = [];
  }
  },
  actions: {

  }
})
